import { useEffect, useState } from 'react';
import styles from './styles/CreateTournament.module.css';

const RegistrationPlayer = () => {
  const [data, setData] = useState({
    firstName: '',
    middleName: '',
    lastName: '',
    region: '',
    birthday: '',
    sportsOrganization: '',
    gender: '',
    sportsCategoryID: '',
    currentAdamovichRank: '',
  });
  const [player, setPlayer] = useState('');
  const [arrayOfCategories, setArrayOfCategories] = useState([]);
  const [created, setCreated] = useState(false);

  function createdchtoto() {
    setCreated(true);
    setTimeout(() => {
      setCreated(false);
    }, 5000);
  }

  useEffect(() => {
    fetch('http://localhost:5000/api/sports-categories')
      .then((response) => response.json())
      .then((data) => setArrayOfCategories(data));
  }, []);

  function handleFormSubmit(event) {
    event.preventDefault();
    console.log(JSON.stringify(data));
    if (data.currentAdamovichRank === '') {
      data.currentAdamovichRank = undefined;
    }

    fetch('http://localhost:5000/api/players', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json;charset=utf-8',
      },
      body: JSON.stringify(data),
    })
      .then((response) =>
        response.ok ? response.json() : Promise.reject(response)
      )
      .then((data) => {
        createdchtoto();
        console.log(data);
      })
      .then((_) => {
        setData({
          firstName: '',
          middleName: '',
          lastName: '',
          region: '',
          birthday: '',
          sportsOrganization: '',
          gender: '',
          sportsCategoryID: '',
          currentAdamovichRank: '',
        });
      })
      .catch((error) =>
        error.json().then((errorData) => {
          setCreated('error');
          setTimeout(() => {
            setCreated(false);
          }, 5000);
          console.error(errorData);
        })
      );
  }
  return (
    <div className="qwert">
      {created === true ? (
        <div>
          <div className={styles.push}>  </div>
        </div>
      ) : (
        ''
      )}
      {created === 'error' ? (
        <div>
          <div className={styles.error}>   </div>
        </div>
      ) : (
        ''
      )}
      <h1> </h1>
      <form className="blockInput" onSubmit={handleFormSubmit}>
        <label className={styles.labelText} htmlFor="firstName">
            :
        </label>
        <br></br>
        <input
          placeholder=""
          type="text"
          value={data.firstName}
          name="username"
          id="firstName"
          onChange={(e) => setData({ ...data, firstName: e.target.value })}
          className={styles.inputText}
        />
        <br></br>

        <label className={styles.labelText} htmlFor="middleName">
            :
        </label>
        <br></br>

        <input
          placeholder=""
          type="text"
          value={data.middleName}
          name="password"
          id="middleName"
          onChange={(e) => setData({ ...data, middleName: e.target.value })}
          className={styles.inputText}
        />
        <br></br>

        <label className={styles.labelText} htmlFor="lastName">
            :
        </label>
        <br></br>

        <input
          placeholder=""
          type="text"
          value={data.lastName}
          name="password"
          id="lastName"
          onChange={(e) => setData({ ...data, lastName: e.target.value })}
          className={styles.inputText}
        />
        <br></br>

        <label className={styles.labelText} htmlFor="gender">
            :
        </label>
        <br></br>
        <select
          name="changeType"
          id="gender"
          className={styles.inputText}
          onChange={(e) => setData({ ...data, gender: e.target.value })}
        >
          <option value="">  </option>
          <option value=""></option>
          <option value=""></option>
        </select>
        <br></br>

        <label className={styles.labelText} htmlFor="region">
            :
        </label>
        <br></br>
        <input
          placeholder=""
          type="text"
          value={data.region}
          name="username"
          id="region"
          onChange={(e) => setData({ ...data, region: e.target.value })}
          className={styles.inputText}
        />
        <br></br>

        <label className={styles.labelText} htmlFor="birthday">
             :
        </label>
        <br></br>
        <input
          type="date"
          id="birthday"
          onChange={(e) => setData({ ...data, birthday: e.target.value })}
          className={styles.inputText}
        />
        <br></br>

        <label className={styles.labelText} htmlFor="sportsCategoryID">
            :
        </label>
        <br></br>
        <select
          name="changeType"
          id="sportsCategoryID"
          className={styles.inputText}
          onChange={(e) =>
            setData({ ...data, sportsCategoryID: e.target.value })
          }
        >
          <option value="">  </option>
          {arrayOfCategories.map((objOfCategory) => {
            return (
              <option key={objOfCategory['_id']} value={objOfCategory['_id']}>
                {objOfCategory['title']}
              </option>
            );
          })}
        </select>
        <br></br>

        <label className={styles.labelText} htmlFor="sportsOrganization">
             :
        </label>
        <br></br>
        <input
          placeholder=""
          type="text"
          value={data.sportsOrganization}
          name="username"
          id="sportsOrganization"
          onChange={(e) =>
            setData({ ...data, sportsOrganization: e.target.value })
          }
          className={styles.inputText}
        />
        <br></br>
        <label className={styles.labelText} htmlFor="currentAdamovichRank">
            :
        </label>
        <br></br>
        <input
          placeholder="982"
          type="text"
          value={data.currentAdamovichRank}
          name="username"
          id="currentAdamovichRank"
          onChange={(e) =>
            setData({ ...data, currentAdamovichRank: Number(e.target.value) })
          }
          className={styles.inputText}
        />
        <br></br>

        <button className={styles.createButton} type="submit">
           
        </button>
      </form>
    </div>
  );
};

